@import url("https://fonts.googleapis.com/css2?family=Dancing+Script&family=Open+Sans:wght@300&display=swap");
:root {
  --ButtonBackground: #7c78b8;
  --ButtonFontColor: #fff;
  --Shadows: #d4d3e8;
  --Background: #5d54a4;
}
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  /*font-family: "Open Sans", sans-serif;*/
  font-family: helvetica, arial;
  text-decoration: none;
  list-style: none;
}
nav .logoImg {
  width: 80px;
  height: 80px;
  position: relative;
  top: 5px;
  background: url("./images//loga_bild.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 10px;
}

/*Action*/

/*everything else*/
nav {
  color: #777;
  align-items: center;
  display: flex;
  height: 100px;
  isolation: isolate;
  align-items: center;
}
.time {
  font-size: 100px;
}
.MainHeader {
  position: relative;
  background-color: #000;
  z-index: 0;
  height: 1000px;
}
.MainHeader::before {
  background-attachment: fixed;

  content: "";
  background: #000 url("./images/pexels-photo-7129737.webp");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  opacity: 0.4;
  inset: 0;
}
.container {
  display: flex;
  width: 100%;
  justify-content: space-between;
  flex-direction: row;
  align-items: center;
  max-width: 90%;
  margin: auto;
}

nav a {
  color: #fff;
  font-weight: bold;
}
.menu,
ul {
  display: flex;
}
ul {
  display: flex;
  gap: 20px;
  font-size: 15px;
}
.buttons {
  display: flex;
  gap: 20px;
}
.buttons button {
  border: none;
  background: var(--ButtonBackground);
  color: #fff;
  width: 140px;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
}
#btn {
  background-color: var(--ButtonBackground);
  border: none;
  padding: 10px;
  width: 120px;
  font-weight: bold;
  cursor: pointer;
  color: crimson;
  box-shadow: 5px 2px 30px var(--Shadows);
  margin-top: 30px;
  color: #fff;
  width: 200px;
  padding: 7px;
  border-radius: 5px;
  cursor: pointer;
}
.buttons button:active,
#btnB:active {
  background-color: var(--Background);
}
header {
  height: 700px;
  color: #fff;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  isolation: isolate;
}
header a {
  color: var(--Shadows);
}
header h1 {
  font-size: 50px;
}
header p {
  font-size: 20px;
  color: aliceblue;
}
.maxW {
  max-width: 100px;
  margin: auto;
  justify-content: space-between;
}

#btnB {
  width: 200px;
  border: 2px solid rgb(235, 161, 3);
  color: rgb(235, 161, 3);
}

.iso {
  isolation: isolate;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 30px;
}
.container2 {
  display: flex;
  width: 100%;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  max-width: 90%;
  margin: auto;
  align-items: center;
}

.iso h1 {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.welcome {
  font-family: "Open Sans", sans-serif;
  font-weight: lighter;
  font-size: 100px;
}

.iso h4 {
  font-weight: lighter;
}
.halal {
  font-size: 60px;
}
.bubble {
  font-size: 60px;
}

#btn:active {
  background: #fff;
  color: #000;
}
.about {
  max-width: 90%;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 100px;
  margin-bottom: 10%;
}
.content {
  display: flex;
  flex-direction: row;
  gap: 10%;
  align-items: center;
  margin-top: 70px;
}
.about h3 {
  color: grey;
  font-size: 20px;
}
.about h2 {
  font-size: 40px;
}
.about span {
  color: crimson;
}
.imageP {
  border-radius: 10px;
  background-color: var(--Background);
}
.imageP img {
  position: relative;
  left: 100px;
  top: 35px;
  height: 400px;
  box-shadow: 20px 2px 40px var(--ButtonBackground);
  border-radius: 10px;
}
.discription p {
  font-size: 15px;
  line-height: 1.8;
}
.discription button {
  background-color: var(--Background);
  border: none;
  padding: 10px;
  width: 120px;
  font-weight: bold;
