  box-shadow: 5px 2px 30px var(--ButtonBackground);
  margin-top: 30px;
}

.discription button:active {
  box-shadow: 1px 2px 20px rgb(85, 1, 1);
  background-color: rgb(105, 5, 25);
}
.famous {
  margin-top: 7%;
}
.display {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 85%;
  margin: auto;
}
.display h2 {
  color: crimson;
}

.adjust {
  width: 500px;
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 0%;
  align-items: center;
  max-height: 700px;
}
.line {
  width: 100px;
  height: 3px;
  background: crimson;
  border-radius: 10px;
}
.title {
  width: 300px;
  max-width: 90%;
  margin: auto;
  margin-bottom: 2%;
  align-items: center;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.title h1 {
  font-size: 40px;
}
.famous {
  align-items: center;
  display: flex;
  flex-direction: column;
}
#btnSpec {
  background-color: crimson;
  border: none;
  padding: 10px;
  width: 220px;
  font-weight: bold;
  cursor: pointer;
  box-shadow: 5px 2px 30px red;
  margin-top: 30px;
}
.stick {
  position: relative;
  top: 100px;
  display: flex;
  flex-direction: column;
  margin-left: 30px;
  width: 30px;
}
.Super-title {
  font-size: 50px;
}
.Super-title span {
  color: #5d54a4;
}
.content_card {
  align-items: center;
  display: flex;
  flex-direction: column;
}
.content_2 {
  position: sticky;
  top: 0;
}
.fai {
  transition: 0.5s;
}
.fai:hover {
  translate: 15%;
}
.stick a {
  font-size: 30px;
}
#facebook {
  color: #4267b2;
}
#twitter {
  color: #333;
}
#linkedin {
  color: #0077b5;
}

::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: rgb(39, 1, 1);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #7c78b8;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.prayer {
  display: flex;
  max-width: 80%;
  margin: auto;
  align-items: center;
  z-index: 1;
}
.remember {
  width: 50%;
  flex-grow: 1;
}
.remember p {
  font-size: 20px;
  line-height: 1.5;
  margin-top: 2%;
}
.contain2 {
  width: 50%;
}
.remember span {
  color: #7c78b8;
}

/* MapComponent.css */

/* Existing CSS */
.Rm {
  overflow-y: hidden;
  display: flex;
  background-color: #ffff;
}
.RM_Container {
  overflow-y: hidden;
}
.RM_Container nav {
  background-color: #000;
}
.Mega nav a {
  color: #fff;
}

/* Updated styles for the containers */
.map-container {
  display: flex;
  height: 100vh;
  flex-direction: row;
  flex: 1;
}

#map {
  flex: 1;
  height: 100%;
  width: 50%;
  position: relative;
  margin: 0; /* Remove any default margin */
  padding: 0; /* Remove any default padding */
}

.MegaContainer {
  height: 100vh;
  overflow-y: auto;
  width: 50%;
  box-sizing: border-box;
  margin: 0; /* Remove any default margin */
  padding: 0; /* Remove any default padding */
  align-items: center;
  overflow-y: hidden;
}

#return {
  box-shadow: 10px 10px 10px -1px #cfcfcf,
    -6px -6px 10px -1px rgba(184, 183, 183, 0.7);
  padding: 10px;
  width: 40px;
  height: 40px;
  position: relative;
  top: 20px;
  left: 30px;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  background-color: #e3edf7;
  color: #000;
}
#return:active {
  box-shadow: 6px 6px 10px -1px rgba(0, 0, 0, 0.15),
    -6px -6px 10px -1px rgba(187, 187, 187, 0.7);
}
.buttons {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 5px;
}

